import { useState } from 'react';
import http from '../../services/http';
import styles from './Modal.module.scss';
import IconPhone from "../../components/UI/icons/IconPhone";
import Cross from '../UI/Modal/Cross/Cross';

const Modal = (props) => {
    const { open, onClose, isSubmitted, setIsSubmitted } = props;

    const [name, setName] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [message, setMessage] = useState('');

    const token = process.env.REACT_APP_TOKEN;
    
    const sendMessage = (e) => {
        e.preventDefault();
        const text = `
         : ${name}                                                                                                         
         : ${phoneNumber}                                                                                               
         : ${message}
       `
        const url = `https://api.telegram.org/bot${token}/sendMessage?chat_id=-935795902&text=${text}`;
        http(url, "GET");
        setName('');
        setPhoneNumber('');
        setMessage('');
        setIsSubmitted(true);
       }

    if (open) {
        return (
            <div className={styles.modal__background}>
                {!isSubmitted ?    
                <div className={styles.modal__card}>            
                    <div className={styles.modal__header}>
                        <div>
                            <h3>  </h3>
                        </div>
                        <div className={styles.modal__close} onClick={onClose}>
                            <Cross />
                        </div>
                    </div>
                   
                    <div className={styles.modal__content}>
                        
                        <div className={styles.modal__FormWrapper}>
                            <form onSubmit={(e) => sendMessage(e)} className={styles.modal__form}>
                                <div className={styles.modal__formInputWrapper}>
                                    <input 
                                     className={styles.modal__input} 
                                     type="text" 
                                     placeholder=" "
                                     value={name}
                                     required
                                     onChange={(e) => setName(e.target.value)} 
                                     />
                                </div>
                                <div className={styles.modal__formInputWrapper}>
                                    <input
                                     className={styles.modal__input} 
                                     type="text" 
                                     placeholder=" "
                                     value={phoneNumber}
                                     required
                                     onChange={(e) => setPhoneNumber(e.target.value)} 
                                     />
                                </div>
                                <div className={styles.modal__formInputWrapper}>
                                    <input 
                                     className={styles.modal__input} 
                                     type="text"
                                     placeholder=" "
                                     value={message}
                                     required
                                     onChange={(e) => setMessage(e.target.value)} 
                                    />
                                </div>
                                <div className={styles.modal__formBtnWrapper}>
                                    <button type="submit" className={styles.modal__btn}></button>
                                </div>
                            </form> 
                        </div>
                        <div className={styles.modal__img}>
                            <IconPhone />
                        </div>
                    </div>
                </div>

                : 
                    
                <div className={styles.modal__card}>            
                 <div className={styles.modal__header}>
                     <div>
                         <h3 className={styles.modal__thanksHeadline}>  !</h3>
                     </div>
                     <div className={styles.modal__close} onClick={onClose}>
                         <Cross />
                     </div>
                 </div>
                
                 <div className={styles.modal__content}>
                     <div className={styles.modal__contentThanks}>
                        <p>  ,        .</p>
                        <p>   ,    
                            <a className={styles.modal__contentThanksHighlighted} href="https://www.instagram.com/_an_slastenko_/?igshid=YmMyMTA2M2Y%3D" target="_blank" rel="noopener noreferrer">  Instagram!</a>
                        </p>
                        <p>       ,    !</p>
                        <p>       ,  : </p>
                        <p className={styles.modal__contentThanksNumber}>+375 29 897-97-70 </p>
                     </div>
                 </div>
             </div>  
            }
            </div>
        );
    }
};

export default Modal;
